import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';

const axios = require('axios');


function Teams(){
    const [teams,setTeams] = useState(null);
    if(teams === null){
        axios.get('https://api.overwatchleague.com/teams?lang=fr')
        .then(function (response) {
            setTeams(response.data.competitors);
        })
        .catch(function (error) {
            console.log(error);
        });
        return (
            <div>
            <h2>Teams</h2>
            </div>
            );
        }else{
            const arrayTeams = teams.map((team)=>{
                return (<Team team={team.competitor} key={team.competitor.id}></Team>)
            })           
            return(<div className="Teams">
            <h2>Teams</h2>
            <div className="listTEams flex center six">
            {arrayTeams}
            </div>
            
            </div>)
            
        }
    }
    
    function Team({team}){
        return(
            <Link to={`/team/${team.id}`}>
            <div className="team">
            {/* {team.abbreviatedName} */}
            <div className="name">{team.name}</div>
            <div className="img"><img src={team.logo} alt={team.alt}/></div>
            </div>
            </Link>
            )
        }
        
        
        export default withRouter(Teams);