import React, { useState } from 'react';

const axios = require('axios')

function LeaderBoard(){
    
    //https://api.overwatchleague.com/ranking
    
    const [rankingTeams,setRankingTeams] = useState(null);
    const [rankingPlayers,setRankingPlayers] = useState(null);
    
    if(rankingTeams === null && rankingPlayers === null){
        axios.get('https://api.overwatchleague.com/ranking')
        .then(function (response) {
            
            const test =  response.data.content.sort(function(a, b){
                return a.placement - b.placement
            })
            setRankingTeams(test);
        })
        .catch(function (error) {
            console.log(error);
        });
        axios.get('api/getPlayersRanking')
        .then(function (response) {
            setRankingPlayers(response.data);
        })
        .catch(function (error) {
            console.log(error);
        });
        
        
        return(
            <div>
            LeaderBoard
            </div>
            )
        }else{
            let PlayersRanking;
            let TeamRankings;
            if(rankingPlayers !== null && rankingTeams !== null){
                let pos = 1;
                PlayersRanking = rankingPlayers.map((player, key)=>{
                    if(rankingPlayers[key-1]){
                        if(rankingPlayers[key-1].score != rankingPlayers[key].score){
                            pos += 1;
                        }
                    }
                    return (<PlayerRanking player={player} key={player.name} pos={pos}></PlayerRanking>)
                })
                TeamRankings = rankingTeams.map((team )=>{
                    return (<TeamRanking team={team} key={team.competitor.id}></TeamRanking>)
                })
            }
            
            
            return(
                <div className="LeaderBoard">
                <h2>Classement</h2>
                <div className="flex center">
                <div className="LeaderBoardTeams">
                <h3>Teams</h3>
                <div className="teamRanking flex">
                <div className="placement">pos</div>
                <div className="img"></div>
                <div className="name">Equipe</div>
                
                <div className="win">W</div>
                <div className="draw">D</div>
                <div className="lose">L</div>
                <div className="play">P</div>
                </div>
                {TeamRankings}
                </div>
                <div className="LeaderBoardPlayers">
                <h3>Players</h3>
                <div className="playerRanking flex">
                <div className="placement">POS</div>
                <div className="loveTeam">❤</div>
                <div className="name">Pseudo</div>
                <div className="score">Score</div>
                </div>
                {PlayersRanking}
                </div>
                </div>
                
                
                </div>
                
                )
            }
        }
        
        
        function TeamRanking({team}){
            return(
                <div className="teamRanking flex">
                <div className="placement">{team.placement}</div>
                <div className="img"><img src={team.competitor.logo} alt=""/></div>
                <div className="name">{team.competitor.name}</div>
                
                <div className="win">{team.records[0].matchWin}</div>
                <div className="draw">{team.records[0].matchDraw}</div>
                <div className="lose">{team.records[0].matchLoss}</div>
                <div className="play">{team.records[0].matchBye}</div>
                </div>
                )
            }
            
            function PlayerRanking({player, pos}){
                return (
                    <div className="playerRanking flex">
                    <div className="placement">{pos}</div>
                    <div className="loveTeam">{player.loveTeam}</div>
                    <div className="name">{player.name}</div>
                    <div className="score">{player.score}</div>
                    </div>
                    );
                }
                
                
                export default LeaderBoard;