import React, { useRef, useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Modal from '../../components/Modal';

const axios = require('axios');

function SignIn({history}){
    const login = useRef();
    const password = useRef();
    const passwordtwo = useRef();
    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false){
        setModal({
            show: !modal.show,
            message: (clean)? modal.message : ""
        })
    }
    
    function sendSignIn(){
        console.log(login.current.value, password.current.value, passwordtwo.current.value)
        const newPlayer = {
            login: login.current.value,
            password:password.current.value,
            passwordtwo:passwordtwo.current.value,
            
        }
        if(newPlayer.login.length > 3 && newPlayer.password.length > 3 && newPlayer.passwordtwo.length > 3){
            if(newPlayer.password.length === newPlayer.passwordtwo.length){
                
                axios.post('api/signIn', {
                    login: login.current.value,
                    pass: password.current.value
                })
                .then(function (response) {
                    if(response.data.status){
                        cookie.save('login', true, { path: '/' });
                        cookie.save('user', response.data.data, { path: '/' });
                        cookie.save('bets', response.data.data.bets, { path: '/' });
                        console.log(response.data.data)
                        setModal({
                            show: true,
                            message: response.data.message
                        });
                        
                        setTimeout(()=>{
                            history.push('/'); 
                        }, 5000)
                        
                    }else{
                        setModal({
                            show: true,
                            message: response.data.message
                        });
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
            else{
                setModal({
                    show: true,
                    message: "Les deux mot de passe que tu as rentré ne sont pas identique "
                });
            }
        }else{
            setModal({
                show: true,
                message: "Ton Pseudo et/ou ton Mot de passe doivent faire plus de 3 caractères"
            });
        }
        
        
        
    }
    return(<div>
        <Modal modal={modal} toggleModal={toggleModal}></Modal>
        <h2>SIGNIN</h2>
        <div className="loginFrom">
        <label htmlFor="login">Pseudo :</label>  
        <input type="text" name="login" id="login" ref={login}/>
        <label htmlFor="password">Password :</label>  
        <input type="password" name="password" id="password" ref={password}/>
        <label htmlFor="passwordtwo">Deux fois :</label>  
        <input type="password" name="passwordtwo" id="passwordtwo" ref={passwordtwo}/>
        
        <div className='btn' type="submit" onClick={()=>{sendSignIn()}}>Valider</div>
        </div>
        
        
        
        </div>)
        
    }
    
    export default withRouter(SignIn);